$white: #EDEDED;
$gray: #BFC0C0;
$dark: #585959;
$light: #D3DEEA;

@import url('https://fonts.googleapis.com/css?family=Abril+Fatface|Lato');

$big: 'Abril Fatface', serif;
$body: 'Lato', sans-serif;

body {
    background: $light;
}

.top {
    margin-top: 30px;
}

.bottom {
    margin-top: 10px;
}

/*text styling*/
h1 {
    font-family: $big;
    color: $white;
    text-align: center;
    font-size: 9em;
    margin: 0;
    text-shadow: -1px 0 $gray, 0 1px $gray, 1px 0 $gray, 0 -1px $gray;
}
h3 {
    font-family: $body;
    font-size: 2em;
    text-transform: uppercase;
    text-align: center;
    color: $gray;
    margin-top: -20px;
    font-weight: 900;
}
p {
    text-align: center;
    font-family: $body;
    color: $dark;
    font-size: .6em;
    margin-top: -20px;
    text-transform: uppercase;
}
